












.alerts {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  z-index: 1;
}
.alerts div {
  background: #bbb;
  overflow: hidden;
}
.alerts div.error {
  background: #d32f2f;
  color: #fff;
}
.alerts div.success {
  background: #66bb6a;
  color: #fff;
}
.alerts p {
  width: 480px;
  margin: 0 auto;
  padding: 4px;
  text-align: center;
}
.expand-transition {
  max-height: 200px; /* height 0 -> auto is not animatable, so use max-height 0 -> large height */
  transition: max-height 1s ease;
}
.expand-enter,
.expand-leave {
  max-height: 0;
}



