






























.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  padding: 20px;
  padding-bottom: 10px;
  margin: 12px 0;
  width: 240px; /* collumn size */
  transition: box-shadow 0.2s, transform 0.2s;
  cursor: default;
}
.note.medium {
  width: 496px; /* medium = (col * 2) + gutter | 496px = (240px * 2) + 16px */
}
.note h2 {
  font-size: 1.2em;
  margin-bottom: 18px;
  word-wrap: break-word;
}
.note pre {
  font-size: 1em;
  margin-bottom: 25px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: inherit;
}
.note button {
  color: #777;
  border: none;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin: 0 4px 0 0;
  float: right;
  margin-left: 5px;
  padding: 5px 10px;
  transform: translateX(15px);
}

.note:hover,
.note:focus {
  box-shadow: 0 7px 10px 0px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
  cursor: pointer;
}
.note:hover button,
.note:focus button {
  /* opacity: 0.6; */
}

.note button.edit {
}

.note button.edit:hover {
  color: #66bb6a;
}

.note button.delete:hover {
  /* background: #d32f2f; */
  /* color: white; */
  color: #d32f2f;
}



