




































































.auth-form{
  width: 480px;
  max-width: 100%;
  margin: 25vh auto 15px;
  background: #fff;
  padding: 15px;
  border-radius: 2px;
  box-shadow: 0 1px 5px #ccc;
}
.auth-form h1{
  font-weight: 300;
}
.auth-form > div {
  margin-top: 15px;
}
.auth-form input {
  height: 32px;
  border: none;
  border-bottom: 2px solid #bbb;
}
.auth-form input:focus{
  border-bottom-color: #555;
}
.auth-form label, .auth-form input{
  display: block;
  width: 100%;
}
.auth-form button {
  font-size: 18px;
  background: #fff;
  border: 1px solid #41b883;
  padding: 4px 6px;
  margin: 0;
  border-radius: 3px;
	cursor: pointer;
}
.auth-form .btn-group button{
  border-radius: 3px 0 0 3px;
  width: 50%;
  float: left;
}
.auth-form .btn-group button + button{
  border-radius: 0 3px 3px 0;
  border-left: none;
}

.auth-form button.disabled{
	background: #ccc;
	color: #999;
	cursor: default;
}

.auth-form .signup-submit{
  width: 100%;
}
.auth-form hr{
  margin-top: 20px;
}
.auth-form .social-providers{
  text-align: center;
}
.auth-form .social-providers a{
  color: #41b883;
  font-size: 36px;
  padding: 4px;
}



