































form.create-note {
  position: relative;
  max-width: calc(100% - 80px);
  width: 500px;

  height: 200px;
  max-width: 100%;
  margin: 30px auto;
  margin-top: 0;
  background: #fff;
  padding: 20px;
  border-radius: 2px;
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
}
form.create-note textarea {
  margin-top: 15px;
}

form.create-note input,
form.create-note textarea {
  width: 100%;
  max-width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  resize: none;
}
form.create-note button {
  position: absolute;
  right: 10px;
  bottom: 10px;
  background: #66bb6a;
  color: #fff;
  border: none;
  border-radius: 4px;
  width: 80px;
  height: 40px;
  cursor: pointer;
  outline: none;
  font-size: 20px;
  transition: background-color 0.2s;
}

form.create-note button:hover {
  background: #43a047;
}



