  position: relative;
}

.app-container {
  width: 70%;
  height: 100vh;
  overflow-y: auto;
  padding: 10px;
  padding-top: 30px;
  background-color: #f5f5f5;
}

.guide-container {
  width: 30%;
  height: 100%;
  background: white;
  min-height: calc(100vh - 100px);
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: scroll;
  box-shadow: -5px 0 10px 0 rgba(0, 0, 0, 0.05);
}

.item-section {
  background: #fff;
  padding-bottom: 1px;
  /* margin-bottom: 20px; */
  transition: background-color 0.3s;
}

.item-section:hover {
  background: #b0bec5;
}

.item-section.open {
  background: #b0bec5;
}

.item-section.open:nth-child(even) {
  background: #cfd8dc;
}

.item-heading {
  padding: 13px 0;
  padding-left: 15px;
  font-size: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #333;
}

.item-heading i {
  margin-right: 15px;
}

.item-heading .fa-caret-down {
  margin-left: auto;
  margin-right: 20px;
  transition: transform 0.3s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.item-heading.open .fa-caret-down {
  transform: rotate(180deg);
}

.item-heading:hover {
  cursor: pointer;
}

.guide-item {
  background: #fff;
  padding: 15px;
  font-size: 15px;
  margin: 15px;
  margin-top: 0;
  border-radius: 3px;
}

.guide-item:last-child {
}

.guide-item pre {
  font-size: 14px;
  overflow-x: auto;
  margin-bottom: 15px;
}

.guide-item p {
  margin-bottom: 15px;
}

.guide-item code {
  word-break: break-all;
}

.guide-item a {
  color: blue;
  word-break: break-all;
}

.guide-item a:hover {
  text-decoration: none;
}

.file-line {
  color: gray;
  font-style: italic;
}



